
#app {
  width: 800px;
  margin: 0 auto;
}
#app .container {
    border: 1px solid;
    margin: 20px;
    height: 400px;
    text-align: center;
}
#app .test1 {
    width: 100px;
    height: 100px;
    line-height: 100px;
    border: 1px solid red;
    text-align: center;
}
#app .test2,
  #app .test3 {
    width: 160px;
    height: 50px;
    border: 1px solid blue;
    text-align: center;
    line-height: 50px;
}
#app .test3 {
    border: 1px solid green;
}
#app .test4 {
    font-size: 12px;
    width: 260px;
    height: 260px;
    border: 1px solid #ccc;
    text-align: center;
    line-height: 50px;
    color: white;
}
#app .test4 .test4-drag {
      width: 100%;
      height: 50px;
      line-height: 50px;
      background: black;
      opacity: .5;
}
#app .test4 .test4-content {
      font-size: 40px;
      line-height: 210px;
      height: 210px;
      text-align: center;
      color: black;
      width: 100%;
}
#app .test5 {
    background: #ccc;
    height: 140px;
    width: 350px;
    line-height: 140px;
    text-align: center;
    opacity: .7;
}
#app .test6 {
    width: 400px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    background: yellow;
    opacity: .7;
}



